<template>
    <div class="data-filter" :style="{
        width: wrapWidth + 'px'
    }">
        <slot/>
        <el-input placeholder="请输入关键词" v-model="filter.keyword" class="input-with-select">
            <el-select v-model="filter.type" slot="prepend" placeholder="请选择">
                <el-option label="标题" :value="0"/>
                <el-option label="描述" :value="1"/>
            </el-select>
            <el-button slot="append" icon="el-icon-search" @click="onSubmit"/>
        </el-input>
    </div>
</template>

<script>
export default {
    data: () => ({
        filter: {
            type: 0,
            keyword: '',
        }
    }),
    methods: {
        onSubmit() {
            this.$emit('filter', this.filter)
        }
    },
    computed: {
        wrapWidth() {
            return this.$slots.default ? 520 : 350
        }
    },
}
</script>

<style scoped>
.data-filter {
    display: flex;
}
</style>

<style>
.input-with-select {
    width: 350px;
}

.input-with-select .el-input {
    width: 80px;
}

.input-with-select .el-input-group__prepend {
    background-color: #fff;
}
</style>
