<template>
    <div class="resource">
        <div class="actions">
            <el-button @click="addItem" type="success" icon="el-icon-folder-add">添加资源</el-button>
            <DataFilter @filter="filterData">
                <el-select v-model="filter.alias" :style="{
                    marginRight: '10px'
                }" clearable placeholder="请选择资源分类">
                    <el-option
                    v-for="alias in aliasList"
                    :key="alias.id"
                    :label="alias.name"
                    :value="alias.id"/>
                </el-select>
            </DataFilter>
        </div>
        <el-table class="full-width" :data="rows" v-loading="loading" stripe border @row-click="rowClick">
            <el-table-column
                v-for="(column, index) in columns"
                :key="index"
                :prop="column.name"
                :label="column.title"
                :width="column.width"
                :min-width="column.minWidth"
                :show-overflow-tooltip="true"
                :fixed="column.fixed"
                sortable
            >
                <template slot-scope="scope">
                    {{ column.name === 'alias' ? aliasFilter(scope.row[column.name]) : (column.format ?
                    column.format(scope.row[column.name]) : scope.row[column.name]) }}
                </template>
            </el-table-column>
            <el-table-column label="操作" width="201" fixed="right">
                <template slot-scope="scope">
                    <el-button size="mini" title="升级/修复资源版本" type="primary" circle @click="upgradeItem(scope.row)" icon="el-icon-upload"/>
                    <el-button size="mini" title="下载资源" type="success" circle @click="downloadItem(scope.row)" icon="el-icon-download"/>
                    <el-button size="mini" title="回滚版本" type="danger" circle @click="delVersion(scope.row)" icon="el-icon-back"/>
                    <el-button size="mini" title="资源编辑" type="warning" circle @click="editItem(scope.row)" icon="el-icon-edit"/>
                    <el-button size="mini" title="资源删除" type="danger" circle @click="removeItem(scope.row)" icon="el-icon-delete"/>
                </template>
            </el-table-column>
        </el-table>
        <div class="paging padding">
            <el-pagination background layout="prev, pager, next" :total="filterList.length" :current-page.sync="currentPage" :hide-on-single-page="true"/>
        </div>
        <el-dialog :title="dialogTitle" :visible.sync="dialogShow" width="450px" :modal-append-to-body="false" @closed="cancel(isPub ? 'upgrade' : 'form')">
            <el-form :model="file" :rules="rules" ref="form" label-width="100px" v-if="!isPub">
                <el-form-item prop="name" label="资源名称">
                    <el-input v-model="file.name" placeholder="请输分类名称"/>
                </el-form-item>
                <el-form-item prop="alias" label="资源分类">
                    <el-select v-model="file.alias" placeholder="请选择资源分类">
                        <el-option v-for="(item, index) in aliasList" :key="index" :label="item.name" :value="item.id"/>
                    </el-select>
                </el-form-item>
                <el-form-item prop="desc" label="资源描述">
                    <el-input v-model="file.desc" placeholder="请输入资源描述"/>
                </el-form-item>
            </el-form>
            <el-form :model="file" :rules="rules2" ref="upgrade" label-width="100px" v-else>
                <el-form-item label="操作类型" >
                    <el-select v-model="versionType" placeholder="请选择">
                        <el-option :label="'升级版本'" :value="1"></el-option>
                        <el-option :label="'修复版本'" :value="2"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item prop="bin" label="资源文件" @click.native="pickFile">
                    <el-input v-model="file.bin" placeholder="点击选择一个文件" :disabled="uploadProcessing"/>
                    <div class="file-field">
                        <input ref="file_field" type="file" @change="setFile"/>
                    </div>
                </el-form-item>
                <el-form-item prop="version" label="资源版本" v-if="versionType == 1">
                    <el-input v-model="file.version" :placeholder="fileVersion === '0.0.0' ? '请输入资源版本' : ('当前版本' + fileVersion)" :disabled="uploadProcessing"/>
                </el-form-item>
                <el-form-item label="资源版本" v-else>
                    <el-input v-model="file.version" :placeholder="fileVersion === '0.0.0' ? '请输入资源版本' : ('当前版本' + fileVersion)" :disabled="true"/>
                </el-form-item>
                <el-form-item prop="log" label="更新日志">
                    <el-input type="textarea" v-model="file.log" placeholder="请输入更新日志" :disabled="uploadProcessing"/>
                </el-form-item>
            </el-form>
            <div slot="footer">
                <el-button size="small" @click="closeModal">取消</el-button>
                <el-button type="primary" size="small" :loading="uploadProcessing && isPub" @click="submit(isPub ? 'upgrade' : 'form')">{{ uploadProcessing ? '提交中...' : ((isPub || isEdit) ? '保存' : '下一步') }}</el-button>
            </div>
        </el-dialog>
        <el-drawer
            title="该资源列表"
            :visible.sync="drawer"
            :size="'60%'"
        >
            <div style="padding:0 20px">
                <el-table :data="versionList" v-loading="versionLoading" stripe border>
                    <el-table-column
                        v-for="(column, index) in versionColumns"
                        :key="index"
                        :prop="column.name"
                        :label="column.title"
                        :width="column.width"
                        :min-width="column.minWidth"
                        :show-overflow-tooltip="true"
                    >
                    </el-table-column>
                    <el-table-column label="操作" width="100" fixed="right">
                        <template slot-scope="scope">
                            <el-button size="mini" title="修复资源" circle type="warning" @click="updateHistory(scope.row)" icon="el-icon-upload"/>
                            <el-button size="mini" title="下载资源" circle type="success" @click="downloadItem(scope.row)" icon="el-icon-download"/>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
             <el-dialog :title="'修复资源'" :modal="false" :visible.sync="fileDrawerShow" width="450px" @closed="fileDrawerShow = false">
                <el-form :model="fileDrawer" :rules="rules2" label-width="100px" ref="fileDrawer">
                    <el-form-item prop="bin" label="资源文件" @click.native="pickFile">
                        <el-input v-model="fileDrawer.bin" placeholder="点击选择一个文件" :disabled="uploadProcessing"/>
                        <div class="file-field">
                            <input ref="file_field" type="file" @change="setFile"/>
                        </div>
                    </el-form-item>
                    <el-form-item label="资源版本">
                        <el-input v-model="fileDrawer.version" :placeholder="('当前版本' + fileDrawer.version)" :disabled="true"/>
                    </el-form-item>
                    <el-form-item prop="log" label="更新日志">
                        <el-input type="textarea" v-model="fileDrawer.log" placeholder="请输入更新日志" :disabled="uploadProcessing"/>
                    </el-form-item>
                </el-form>
                <div slot="footer">
                    <el-button size="small" @click="fileDrawerShow = false">取消</el-button>
                    <el-button type="primary" size="small" :loading="uploadProcessing " @click="submitDrawer">保存</el-button>
                </div>
            </el-dialog>
        </el-drawer>
    </div>
</template>

<script>
    import * as http from '@/libs/request';
    import { getToken } from '@/libs/auth';
    import { formatDate } from '@/libs/moment';
    import { ObjectToFormdata } from '@/libs/formdata';
    import { columns, versionColumns } from '@/mixins/Resource';
    import DataFilter from '@/components/datafilter';
    var fileVersion = '0.0.0';

    export default {
        data: () => ({
            columns,
            versionColumns,
            list: [],
            filterList: [],
            filter: {
                alias: ''
            },
            currentPage: 1,
            aliasList: [],
            isEdit: false,
            dialogTitle: '',
            dialogShow: false,
            file: {
                id: '',
                name: '',
                alias: '',
                bin: '',
                version: '',
                desc: '',
                log: '',
            },
            fileDrawer: {
                id: '',
                bin: '',
                version: '',
                log: '',
                resource_id: ''
            },
            bin: null,
            isPub: false,
            rules: {
                name: [
                    { required: true, message: '资源名称不能为空', trigger: 'blur' }
                ],
                alias: [
                    { required: true, message: '指定一个资源分类', trigger: 'change' }
                ],
                desc: [
                    { required: true, message: '资源描述不能为空', trigger: 'blur' }
                ],
            },
            rules2: {
                bin: [
                    { required: true, message: '选择一个文件', trigger: 'change' }
                ],
                version: [
                    { required: true, message: '资源版本不能为空', trigger: 'blur' },
                    {
                        validator: (rule, value, callback) => {
                            let reg = /^\d+\.\d+\.\d+$/;
                            if (!reg.test(value)) {
                                callback(new Error('请输入合法的版本号, 例如 x.x.x '))
                            } else {
                                callback()
                            }
                        }, trigger: 'blur'
                    },
                    {
                        validator: (rule, value, callback) => {
                            let [u, v, w] = value.split('.');
                            let [x, y, z] = fileVersion.split('.');
                            let p = v => parseInt(v);
                            if(p(u) > p(x) || (p(u) === p(x) && p(v) > p(y)) || (p(u) === p(x) && p(v) === p(y) && p(w) > p(z))) {
                                callback()
                            }
                            else {
                                callback(new Error('发布的版本号不能低于当前版本号, 当前版本号 ' + fileVersion))
                            }
                        }, trigger: 'blur'
                    }
                ],
            },
            uploadProcessing: false,
            loading: false,
            fileVersion,
            versionType: 1,
            drawer: false,
            versionLoading: false,
            versionList: [],
            fileDrawerShow: false,
            fileDrawerLoading:false
        }),
        components: { DataFilter },
        methods: {
            listResource() {
                this.loading = true;
                http.listResource(null, {
                    success: res => {
                        this.filterList = this.list = res ? res.map(({id, name, type, file, desc, log, version, download, created_at = '', updated_at = ''}) => {
                            return {
                                id,
                                name,
                                alias: type,
                                bin: file,
                                desc,
                                log,
                                version,
                                download,
                                created_at: formatDate(created_at),
                                updated_at: formatDate(updated_at),
                            }
                        }) : [];
                        this.loading = false;
                    },
                    fail: err => {
                        this.$message.error('获取资源列表失败, ' + err);
                        this.loading = false;
                    }
                })
            },
            listAlias(callback) {
                http.listAlias({
                    success: res => {
                        this.aliasList = res.resource_types ? res.resource_types.map(item => {
                            return {
                                id: item.id,
                                name: item.name,
                            }
                        }) : [];
                        callback()
                    },
                    fail: err => {
                        this.$message.error('获取资源分类失败, ' + err)
                    }
                })
            },
            addItem() {
                this.dialogTitle = "添加资源";
                this.isEdit = false;
                this.dialogShow = true;
            },
            editItem({id, name, alias, desc}) {
                this.dialogTitle = "编辑资源";
                this.isEdit = true;
                this.file = {...this.file, ...{id, name, alias, desc}};
                this.dialogShow = true;
            },
            closeModal() {
                this.dialogShow = false;
            },
            historyItem() {
                //
            },
            upgradeItem({id, name, version, alias, desc}) {
                this.isPub = true;
                this.dialogTitle = "升级/修复资源版本";
                this.file = {...this.file, ...{id, name, alias, desc}};
                fileVersion = this.fileVersion = version;
                this.dialogShow = true;
            },
            pickFile(ev) {
                let { file_field } = this.$refs;
                file_field.click();
                setTimeout(() => {
                    ev.target.blur()
                }, 100);
            },
            setFile(ev) {
                let files = ev.target.files;
                console.log(files)
                if (files.length > 0) {
                    if(this.fileDrawerShow){
                        this.bin = files[0];
                        this.fileDrawer.bin = files[0].name;
                    }else{
                        this.bin = files[0];
                        this.file.bin = files[0].name;
                    }
                } else {
                    this.bin = null;
                }
            },
            cancel(type) {
                this.reset(type);
                this.isPub = false;
                this.isEdit = false;
                fileVersion = this.fileVersion = '0.0.0';
            },
            reset(form) {
                this.file = {
                    name: '',
                    alias: '',
                    bin: '',
                    version: '',
                    desc: '',
                    log: '',
                };
                this.bin = null;
                this.$refs[form].resetFields();
            },
            submit(form) {
                this.$refs[form].validate(valid => {
                    if (!valid) {
                        return false;
                    } else {
                        if (form === 'form') {
                            this.saveFile()
                        } else if (form === 'upgrade') {
                            this.upgradeResource()
                        }
                    }
                })
            },
            upgradeResource() {
                this.uploadProcessing = true;
                let callback = {
                    success: res => {
                        this.dialogShow = false;
                        this.upgradeFileDialog = false;
                        this.uploadProcessing = false;
                        this.listResource();
                        this.bin = null;
                    },
                    fail: err => {
                        this.uploadProcessing = false;
                        this.$message.error('操作失败, ' + err);
                        this.bin = null;
                    }
                };
                let {id, name, desc, alias, version, log} = this.file;
                let formData = ObjectToFormdata({
                    name,
                    desc,
                });
                formData.append('resource_id', id);
                formData.append('file', this.bin);
                formData.append('log', log);
                if(this.versionType == 1){
                    formData.append('version', version);
                    http.upgradeResource(formData, callback)
                }else{
                    http.resourceUplad(formData, callback)
                }
            },
            saveFile() {
                let callback = {
                    success: ({ id }) => {
                        if(!this.isEdit) {
                            this.isPub = true;
                            this.file.id = id;
                            this.dialogTitle = "更新资源版本";
                        }
                        else {
                            this.dialogShow = false;
                            this.listResource();
                        }
                    },
                    fail: err => {
                        this.$message.error('操作失败, ' + err)
                    }
                };
                let {id, name, desc, alias} = this.file;
                let formData = ObjectToFormdata({
                    name,
                    type: alias,
                    desc,
                });
                if (this.isEdit) {
                    formData.append('id', id);
                    http.updateResource(formData, callback)
                } else {
                    http.addResource(formData, callback)
                }
            },
            removeItem(row) {
                this.$confirm('此操作将永久删除该资源, 是否继续?', '提示', {
                    type: 'warning'
                }).then(() => {
                    this.deleteResource(row.id)
                })
            },
            deleteResource(id) {
                http.deleteResource({
                    id
                }, {
                    success: res => {
                        this.listResource()
                    },
                    fail: err => {
                        this.$message.error('获取资源失败, ' + err)
                    }
                })
            },
            aliasFilter(val) {
                let filter = this.aliasList.filter(alias => alias.id === val)
                return filter.length ? filter[0].name : '未分类'
            },
            filterData({ type, keyword }) {
                let { alias } = this.filter;
                let _keyword = keyword.toLocaleLowerCase();
                this.filterList = this.list.filter(d => {
                    return (alias === '' || d.alias === alias) && d[type === 0 ? 'name' : 'desc'].toLocaleLowerCase().indexOf(_keyword) > -1;
                })
            },
            downloadItem({ id, version, file, resource_id }) {
                let link = document.createElement('a');
                if(this.drawer){
                    link.href = http.getBaseUrl() + 'resource/download?id=' + resource_id + '&version=' + version + '&token=' + getToken();
                }else{
                    link.href = http.getBaseUrl() + 'resource/download?id=' + id + '&version=' + version + '&token=' + getToken();
                }
                link.download = file;
                link.click();
            },


            rowClick(row, column){
                if(column.property == "name"){
                   this.drawer = true
                   this.getVersionList(row.id)
                }
            },

            getVersionList(id){
                this.versionLoading = true
                http.getversionList({resource_id: id},{
                    success: res => {
                        this.versionLoading = false
                        this.versionList = res.resources
                    },
                    fail: err => {
                        this.versionLoading = false
                        this.$message.error(err)
                    }
                })
            },

            delVersion({id}){
                this.$confirm('此操作将回退到上一版本, 是否继续?', '提示', {
                    type: 'warning'
                }).then(() => {
                    console.log(id)
                    http.delVersion({resource_id: id},{
                       success: res => {
                           this.listResource()
                       },
                       fail: err => {
                           this.$message.error(err)
                       }
                   })
                })
            },

            updateHistory({version, resource_id, id}){
                this.fileDrawerShow = true;
                this.fileDrawer.resource_id = resource_id
                this.fileDrawer.id = id
                this.fileDrawer.version = version
            },
            submitDrawer(){
                 this.$refs.fileDrawer.validate(valid => {
                    if (!valid) {
                        return false;
                    } else {
                        this.uploadProcessing = true
                        let { id, version, log, resource_id } = this.fileDrawer
                        let formData = ObjectToFormdata({
                            version,
                            resource_id,
                            log,
                            id
                        });
                        formData.append('file', this.bin);
                        http.resourceUplad(formData, {
                            success: res => {
                                this.bin = null
                                this.uploadProcessing = false
                                this.fileDrawerShow = false
                                this.getVersionList(this.fileDrawer.resource_id)
                            },
                            fail: error => {
                                this.$message.error(err)
                                this.uploadProcessing = false
                                this.fileDrawerShow = false
                            }
                        })
                    }
                })
            }
        },
        computed: {
            rows() {
                let limit = (this.currentPage - 1) * 10;
                return this.filterList.slice(limit, limit + 10);
            }
        },
        mounted() {
            this.listAlias(this.listResource);
        },
    }
</script>

<style scoped>
    .actions {
        display: flex;
        justify-content: space-between;
    }
    
    .paging {
        display: flex;
        justify-content: center;
    }

    .file-field {
        height: 0;
        overflow: hidden;
    }
</style>
